export default {
    data() {
        return {
            n: true
        };
    },
    methods: {
        next: function() {
            this.n = false;
        },
        back: function() {
            this.n = true;
        },
        already: function() {
            uni.navigateBack({
                delta: 2
            });
        }
    }
};
